import { GET_PADS, GET_PAD, PAD_LOADING, GET_WHILTELIST, SET_WHILTELIST_STATE } from '../actions/types.js';

const initialState = {
	pads: [],
	whitelist: [],
	whitestate: {},
	loading: false
};

export default function(state = initialState, action) {
	switch (action.type) {
		case PAD_LOADING:
			return {
				...state,
				loading: true
			};

		case GET_PADS:
			return {
				...state,
				pads: action.payload,
				loading: false
			};

		case GET_PAD:
			return {
				...state,
				pad: action.payload,
				loading: false
			};
		case GET_WHILTELIST:
			return {
				...state,
				whitelist: action.payload,
				loading: false
			};
		case SET_WHILTELIST_STATE:
			return {
				...state,
				whitestate: action.payload,
				loading: false
			};
		default:
			return state;
	}
}
