import React, { forwardRef, createContext, useEffect, useState } from 'react';
import { ProSidebar, Menu, MenuItem, SubMenu, SidebarFooter } from 'react-pro-sidebar';
import 'react-pro-sidebar/dist/css/styles.css';
import { ChevronLeft, ChevronRight } from 'react-feather';
import { Link } from 'react-router-dom';
import {
	FaHome,
	FaCentos,
	FaLock,
	FaAirbnb,
	FaSchlix,
	FaDAndD,
	FaMastodon,
	FaDonate,
	FaLink,
	FaMoneyBillAlt,
	FaAudible
} from 'react-icons/fa';
import Logo from '../assets/img/original.png';
import { Modal, Button } from 'rsuite';

var wid = '',
	colWid = '',
	scrnState = { position: 'fixed' };

function Sider({ screen, togglettt }) {
	const [ menuCollapse, setMenuCollapse ] = useState(false);
	const [ open, setOpen ] = React.useState(false);
	const [ size, setSize ] = React.useState();
	const handleOpen = (value) => {
		setSize(value);
		setOpen(true);
	};
	const handleClose = () => setOpen(false);

	const menuIconClick = () => {
		//condition checking to change state from true to false and vice versa
		menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
	};

	const mobile = { position: 'fixed', top: '60px' };
	const desktop = { position: 'fixed', top: '80px' };
	const mobileWidth = '200px';
	const desktopWidth = '350px';
	const mobileColwid = '70px';
	const desktopColwid = '80px';

	if (togglettt) {
		if (screen) {
			scrnState = mobile;
			wid = mobileWidth;
			colWid = mobileColwid;
		} else {
			scrnState = desktop;
			wid = desktopWidth;
			colWid = desktopColwid;
		}
	} else {
		if (screen) {
			scrnState = mobile;
		} else {
			scrnState = desktop;
		}
		wid = '0px';
		colWid = '0px';
	}
	return (
		<ProSidebar
			collapsed={menuCollapse}
			style={scrnState}
			width={wid}
			collapsedWidth={colWid}
			className="sider-bar"
		>
			<Modal size={size} open={open} onClose={handleClose}>
				<Modal.Header />
				<Modal.Body>
					<h1>Coming Soon!</h1>
				</Modal.Body>
				<Modal.Footer>
					<Button onClick={handleClose} appearance="primary">
						Ok
					</Button>
				</Modal.Footer>
			</Modal>
			<Menu iconShape="circle">
				<Link to="/">
					<MenuItem icon={<FaHome />}>Home</MenuItem>
				</Link>
				<SubMenu title="SaFuTrendzPads " icon={<FaCentos />}>
					<MenuItem>
						<Link to="/LaunchPad1">Create LaunchPad </Link>
					</MenuItem>

					<MenuItem size="xs" onClick={() => handleOpen('xs')}>
						Create Fair Launch
					</MenuItem>

					<MenuItem>
						<Link to="/PadList">Launchpad List</Link>{' '}
					</MenuItem>

					<MenuItem>
						<Link to="/CreateToken">Create Token</Link>
					</MenuItem>

					<MenuItem size="xs" onClick={() => handleOpen('xs')}>
						manage presale
					</MenuItem>
					<MenuItem size="xs" onClick={() => handleOpen('xs')}>
						alarm{' '}
					</MenuItem>
				</SubMenu>
				<MenuItem icon={<FaLock />} size="xs" onClick={() => handleOpen('xs')}>
					Locks
				</MenuItem>
				<MenuItem icon={<FaAirbnb />}>
					<a href="https://multisender.safutrendz.com">AirDrop</a>
				</MenuItem>
				<MenuItem icon={<FaSchlix />} size="xs" onClick={() => handleOpen('xs')}>
					Premium Sales{' '}
				</MenuItem>
				<MenuItem icon={<FaAudible />} size="xs" onClick={() => handleOpen('xs')}>
					KYC + Audit{' '}
				</MenuItem>
				<MenuItem icon={<FaMastodon />} size="xs" onClick={() => handleOpen('xs')}>
					Marketing{' '}
				</MenuItem>
				<MenuItem icon={<FaDonate />} size="xs" onClick={() => handleOpen('xs')}>
					Docs{' '}
				</MenuItem>
				<MenuItem icon={<FaLink />}>
					<a href="https://linktree.com/safu_trendz">Social Links</a>
				</MenuItem>
				<MenuItem icon={<FaDAndD />} size="xs" onClick={() => handleOpen('xs')}>
					Custom Dapp{' '}
				</MenuItem>
				<MenuItem
					icon={
						<a href="https://safutrendz.com/">
							<img src={Logo} className="photo" alt="logo" style={{ width: '35px', height: '35px' }} />
						</a>
					}
				>
					SafuTrendz
				</MenuItem>
				<MenuItem onClick={menuIconClick}>
					{!menuCollapse ? (
						<ChevronLeft size={30} className="flux-right" />
					) : (
						<ChevronRight size={30} className="flux-right" />
					)}
				</MenuItem>
			</Menu>
			<SidebarFooter />
			<br />
		</ProSidebar>
	);
}

export default Sider;
